@import "../../Variables.scss";

.education {
    display: flex;
    flex-direction: column;
    height: 240px;

    align-items: center;
    justify-content: space-between;

    .educationDetails {
        text-align: center;
        padding: 15px;
        padding-top: 25px;
        bottom: 0px;

        h2 {
            @include montserratRegular;
            letter-spacing: 2px;
            font-size: 16px;
            margin: 0;
            margin-bottom: 10px;
            color: $Blackish;
        }
        h3 {
            @include charterLight;
            letter-spacing: 2px;
            font-size: 14px;
            margin: 0;
            margin-bottom: 10px;
            color: $Blackish;
        }
        h4 {
            @include charterLight;
            letter-spacing: 5px;
            font-size: 14px;
            margin: 0;
            color: $Blackish;
        }
    }
    .educationPic {
        padding-top: 20px;
        img {
            max-width: 18vw;
            height: auto;
            max-height: 10vh;
        }
    }
    
}
